import {LoadingController, ModalController} from 'ionic-angular';
import { Component } from "@angular/core";
import {
  IonicPage,
  NavController,
  NavParams,
  ViewController
} from "ionic-angular";
import { EmailViewModel } from "../../Model/ViewModel/EmailViewModel";


/**
 * Generated class for the EmailModalPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: "page-email-add-edit-modal",
  templateUrl: "email-add-edit-modal.html"
})
export class EmailAddEditModalPage {
  item: any;
  mode: string;
  CanEditEmail: boolean;
  title:string;
  Email_type:string;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public viewCtrl: ViewController,
    public loadingCtrl: LoadingController,
    public modalCtrl: ModalController,

  ) {
    this.item = new EmailViewModel();
    this.item.exec_group=navParams.data.item.exec_group;
this.item.exec_status_key=navParams.data.item.exec_status_key;
this.item.exec_status=navParams.data.item.exec_status;
this.item.email_id=navParams.data.item.email_id;
this.item.creator=navParams.data.item.creator;
this.item.create_time=navParams.data.item.create_time;
this.item.modifier=navParams.data.item.modifier;
this.item.last_update_time=navParams.data.item.last_update_time;
this.item.email_desc=navParams.data.item.email_desc;
this.item.email_full_desc=navParams.data.item.email_full_desc;

    this.CanEditEmail = navParams.data.CanEditEmail;
    this.mode = navParams.data.mode;
    this.title="Email寄發設定";
  }
  SelectExecStatus() {
    let modal = this.modalCtrl.create("CodeSelectModalPage", {
      select_key: this.item.exec_status_key,
      code_key: "012|C,012|F,012|S",
    });
    modal.onDidDismiss(select_data => {
      if (select_data == null) return;
      this.item.exec_status_key = select_data.code_key;
      this.item.exec_status = select_data.code_desc;
    });

    modal.present();
  }
  SelectEmailID() {
    let modal = this.modalCtrl.create("EmailUserSelectModelPage", {
      select_id: this.item.email_id,
      show_select_add:(this.mode==="POST"?true:false),
    });
    modal.onDidDismiss(select_data => {
      if (select_data == null) return;
      this.item.email_id = select_data.email_id;
      this.item.email_desc=select_data.email_desc;
    });

    modal.present();
  }

  Save() {
    this.viewCtrl.dismiss(this.item);
  }
  close() {
    this.viewCtrl.dismiss();
  }
  ionViewDidLoad() {
    console.log("ionViewDidLoad EmailModalPage");
  }
}



