export class EmailViewModel {
 /// <summary>
///  轉檔群組
/// <summary>
public exec_group: string;
/// <summary>
///  執行狀態主鍵
/// <summary>
public exec_status_key: string;
/// <summary>
///  郵件編號
/// <summary>
public email_id: string;
/// <summary>
///  建立者
/// <summary>
public creator: string;
/// <summary>
///  建立時間
/// <summary>
public create_time: Date;
/// <summary>
///  最後更新者
/// <summary>
public modifier: string;
/// <summary>
///  最後更新時間
/// <summary>
public last_update_time: Date;
/// <summary>
///  執行狀態
/// <summary>
public exec_status: string;

/// <summary>
///  郵件說明
/// <summary>
public email_desc: string;

/// <summary>
///  郵件說明(含代碼)
/// <summary>
public email_full_desc: string;

attach_cnt:number;
attach_color:string;
email_exec_group:string;
}



